@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #e7e7e5;
  line-height: 1.5;
  font-family: 'Press Start 2P', cursive;
}

.title {
  text-align: center;
  color: #e4c439;
  margin-bottom: 1rem;
  font-size: 1rem;
  text-transform: uppercase;
}

.title__image {
  padding-top: 1rem;
  width: 15rem;
}

.deck--four {
  grid-template-columns: repeat(4, 160px);
  display: grid;
  margin: 0 auto;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
}

.deck--five {
  grid-template-columns: repeat(5, 160px);
  display: grid;
  margin: 0 auto;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
}

.loading {
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

@media screen and (min-width: 500px) and (max-width: 850px) {
  .title {
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }

  .title__image {
    width: 8rem;
  }

  .deck--four {
    grid-template-columns: repeat(4, 110px);
    display: grid;
    margin: 0 auto;
    justify-content: center;
    grid-gap: 5px;
    gap: 5px;
  }

  .deck--five {
    grid-template-columns: repeat(5, 110px);
    display: grid;
    margin: 0 auto;
    justify-content: center;
    grid-gap: 5px;
    gap: 5px;
  }
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }

  .title__image {
    width: 8rem;
  }

  .deck--four {
    grid-template-columns: repeat(4, 60px);
    display: grid;
    margin: 0 auto;
    justify-content: center;
    grid-gap: 5px;
    gap: 5px;
  }

  .deck--five {
    grid-template-columns: repeat(5, 60px);
    display: grid;
    margin: 0 auto;
    justify-content: center;
    grid-gap: 5px;
    gap: 5px;
  }

  .loading {
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn {
  padding: 10px;
  margin: 15px;
  background-color: #f4a603;
  border: 2px solid #0a0b88;
  color: #0a0b88;
  border-radius: 5px;
  /* text-transform: uppercase; */
  font-family: 'Press Start 2P', cursive;
}

.card {
  height: 10rem;
  width: 10rem;
  background: #f0da9f;
  color: #fbca1c;
  border-radius: 10px;
  border-top: 0.5px solid #cebf7b;
  border-bottom: 0.5px solid #cebf7b;
  text-align: center;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: 0.2s ease-in-out all;
  border: 3px solid #fbca1c;
}

/* front of card - the picture */
.front .card {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
  font-size: 10px;
}
.flipped .front .card {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of card - cover */
.back__image {
  width: 100%;
  height: 12rem;
  object-fit: contain;
  padding-bottom: 35px;
}
.back .card {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}

.flipped .back .card {
  transform: rotateY(90deg);
  transition-delay: 0s;
}

.card__name {
  color: #333;
}

.card__type {
  color: #464646;
}

@media screen and (max-width: 500px) {
  .card {
    height: 60px;
    width: 60px;
    background: #f0da9f;
    color: #fbca1c;
    border-radius: 10px;
    border-top: 0.5px solid #cebf7b;
    border-bottom: 0.5px solid #cebf7b;
    text-align: center;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s ease-in-out all;
    border: 3px solid #fbca1c;
  }

  .card img {
    width: 100%;
  }

  .back__image {
    width: 100%;
    height: 55px;
    object-fit: contain;
    padding-bottom: 1px;
  }

  .card__name {
    display: none;
  }

  .card__type {
    display: none;
  }
}

@media screen and (min-width: 500px) and (max-width: 850px) {
  .card {
    height: 7rem;
    width: 7rem;
    background: #f0da9f;
    color: #fbca1c;
    border-radius: 10px;
    border-top: 0.5px solid #cebf7b;
    border-bottom: 0.5px solid #cebf7b;
    text-align: center;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s ease-in-out all;
    border: 3px solid #fbca1c;
  }

  .front__image {
    width: 60%;
  }

  .back__image {
    width: 100%;
    height: 100px;
    object-fit: contain;
    padding-bottom: 1px;
  }

  .card__name {
    font-size: 10px;
  }

  .card__type {
    font-size: 10px;
    /* display: none; */
  }
}

.footer {
  font-size: 10px;
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 100vw;
  margin-bottom: 5px;
}

.heart {
  width: 20px;
  margin-bottom: -5px;
}

.link {
  text-decoration: none;
  color: black;
  margin-left: 10px;
  cursor: pointer;
}

.modal {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  /* background-color: rgb(0, 0, 0); */
  background-color: rgb(250, 250, 210, 0.8);
}

/* Modal Content */
.modal__content {
  /* background-color: #f0da9f; */
  margin: auto;
  padding: 3rem;
  /* border: 1px solid #888; */
  height: 50%;
  width: 100vw;
  /* border-radius: 1rem; */
  display: grid;
  place-items: center;
  /* border: 10px solid #fbca1c; */
}

.modal__content h3 {
  padding-bottom: 2rem;
}

.modal__content img {
  width: 150px;
  z-index: 2;
}

.modal__title {
  padding: 1rem;
}

.big {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
}

@media screen and (min-width: 500px) {
  .modal {
    display: grid;
    place-items: center;
  }
}

.moves {
  padding-bottom: 2rem;
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 100vw;
}

.level {
  padding: 0.5rem;
  font-size: 10px;
}

.level__label {
  padding: 1rem;
}

.level__input {
  margin: 0.25rem;
}

@media screen and (max-width: 500px) {
  .level {
    padding: 0.5rem;
    font-size: 10px;
  }

  .level__label {
    padding: 0.25rem;
  }

  .level__input {
    margin: 0.25rem;
  }
}

